    url('icomoon/fonts/icomoon.ttf?7cay7f') format('truetype'),
    url('icomoon/fonts/icomoon.woff?7cay7f') format('woff'),
    url('icomoon/fonts/icomoon.svg?7cay7f#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

